import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import * as reduxFunctions from "../../store/actions/index";
import classNames from "./Header.module.css";

const Header = (props) => {
  const [icon, setIcon] = useState(<i className="fas fa-bars"></i>);

  const { sideMenuToggle } = props;

  useEffect(() => {
    if (!sideMenuToggle) {
      setIcon(<i className="fas fa-bars"></i>);
    } else {
      setTimeout(() => {
        setIcon(<i className="fas fa-times"></i>);
      }, 300);
    }
  }, [sideMenuToggle]);

  const sideMenuToggleClick = () => {
    props.sideMenuToggleFn();
  };
  return (
    <div className={classNames.header}>
      <div className={classNames.logo}>
        <a href="!#">
          <img src="https://zoomcbd.com/math/logo.svg" alt="Logo" />
          Multi Plus
        </a>
      </div>
      <div
        className={classNames.ico}
        onClick={() => {
          sideMenuToggleClick();
        }}
        id="sideBarTrigger"
      >
        {icon}
      </div>
    </div>
  );
};
const mapStatetoProps = (state) => {
  return {
    sideMenuToggle: state.menu.sideMenuToggle,
  };
};
const mapDispatchtoProps = (dispatch) => {
  return {
    sideMenuToggleFn: () => dispatch(reduxFunctions.sideMenuToggle()),
  };
};
export default connect(mapStatetoProps, mapDispatchtoProps)(Header);
