import React, { useEffect } from "react";
import { connect } from "react-redux";
import * as reduxFunctions from "../../store/actions/index";
import classNames from "./Bar.module.css";

const Bar = (props) => {
  useEffect(() => {
    const slideToggle = (e) => {
      e.srcElement.classList.toggle(`${classNames.dropDownTriggerActive}`);
      e.srcElement.nextSibling.classList.toggle(`${classNames.dropDownActive}`);
    };
    document.querySelectorAll("#dropDownTrigger").forEach((cur) => {
      cur.addEventListener("click", slideToggle);
    });
    return () => {
      document.querySelectorAll("#dropDownTrigger").forEach((cur) => {
        cur.removeEventListener("click", slideToggle);
      });
    };
  });
  return (
    <div
      className={classNames.container}
      style={props.sideMenuToggle ? { right: "0px" } : { right: "-100%" }}
      id="sidebar"
    >
      <div className={classNames.dropDownContainer}>
        <div className={classNames.dropDownTrigger} id="dropDownTrigger">
          Preferences
        </div>
        <div className={classNames.dropDown}>
          <button
            onClick={() => {
              props.pheadersToggle();
            }}
          >
            Toggle placement headers
          </button>
          <button
            onClick={() => {
              props.gridToggle();
            }}
          >
            Toggle grid
          </button>
          <button
            onClick={() => {
              props.carryToggle();
            }}
          >
            Toggle carry
          </button>
        </div>
      </div>
    </div>
  );
};

const mapStatetoProps = (state) => {
  return {
    sideMenuToggle: state.menu.sideMenuToggle,
  };
};
const mapDispatchtoProps = (dispatch) => {
  return {
    pheadersToggle: () => dispatch(reduxFunctions.pHeadersToggle()),
    gridToggle: () => dispatch(reduxFunctions.gridToggle()),
    carryToggle: () => dispatch(reduxFunctions.carryToggle()),
  };
};
export default connect(mapStatetoProps, mapDispatchtoProps)(Bar);
