import * as actionTypes from "../actions/actions";

const initialState = {
  inputFocusTop: {
    0: false,
    1: false,
    2: true,
  },
  inputFocusBottom: {
    0: false,
    1: false,
    2: false,
  },
  toggleKeypad: false,
};
const falsyObject = {
  inputFocusTop: {
    0: false,
    1: false,
    2: false,
  },
  inputFocusBottom: {
    0: false,
    1: false,
    2: false,
  },
};
const numberPad = (state = initialState, action) => {
  if (
    action.type === actionTypes.START ||
    action.type === actionTypes.GENERATEDONSTART ||
    action.type === actionTypes.LASTFOCUS
  ) {
    return {
      ...state,
      ...falsyObject,
    };
  }
  if (action.type === actionTypes.END) {
    return {
      ...state,
      ...falsyObject,
      inputFocusTop: {
        ...falsyObject.inputFocusTop,
        2: true,
      },
    };
  }
  switch (action.type) {
    case actionTypes.INPUTFOCUS:
      return {
        ...state,
        ...falsyObject,
        [action.position]: {
          ...falsyObject[action.position],
          [action.current]: true,
        },
      };
    case actionTypes.KEYPRESSMULTI:
      return {
        ...state,
        ...falsyObject,
        inputFocusBottom: {
          ...falsyObject.inputFocusBottom,
          2: true,
        },
      };
    case actionTypes.KEYPADTOGGLE:
      return {
        ...state,
        toggleKeypad: !state.toggleKeypad,
      };
    case actionTypes.KEYPADHIDE:
      return {
        ...state,
        toggleKeypad: false,
      };
    case actionTypes.KEYPADSHOW:
      return {
        ...state,
        toggleKeypad: true,
      };
    default:
      return state;
  }
};

export default numberPad;
