  //     setToggleKeypad();
  //     if(toggler){
  //         numberPad.classList.remove(`${classNames.number_pad_container_shrink}`)
  //         numberPad.classList.add(`${classNames.number_pad_container_grow}`);
  //         setArrow(<i className="fas fa-arrow-circle-right"></i>)
  //     }else{
  //         numberPad.classList.remove(`${classNames.number_pad_container_grow}`);
  //         numberPad.classList.add(`${classNames.number_pad_container_shrink}`)
  //         setArrow(<i className="fas fa-arrow-circle-left"></i>)
  //     }
  // }, [setToggleKeypad])

  // useEffect(()=>{
  //     if(toggleKeypad){
  //         KeypadToggleFn(toggleKeypad)
  //     }
  // },[toggleKeypad, KeypadToggleFn])

  // const {setToggleKeypad, keypadHide, toggleKeypad, keypadShow} = props;

  const [icon, setIcon] = useState(<i className="fas fa-bars"></i>);

  const sideMenuToggleClick = () => {
    props.sideMenuToggleFn();
  };
  const {
    keypadHide,
    toggleKeypad,
    keypadShow,
    sideMenuToggle,
    sideMenuClose,
  } = props;

  useEffect(() => {
    if (!sideMenuToggle) {
      setIcon(<i className="fas fa-bars"></i>);
    } else {
      setTimeout(() => {
        setIcon(<i className="fas fa-times"></i>);
      }, 300);
    }
    const numberPad = document.getElementById("numberPad");
    const sideBar = document.getElementById("sidebar");
    const sideBarTrigger = document.getElementById("sideBarTrigger");
    const mvmenu = document.getElementById("mvmenu");
    const keypadTrigger = document.getElementById("keypadTrigger");
    const board_container = document.getElementById("board_container");

    const keypadShowFn = () => {
      keypadShow();
    };

    const documentClickFn = (e) => {
      if (
        e.target !== numberPad &&
        !numberPad.contains(e.target) &&
        e.target !== keypadTrigger &&
        !keypadTrigger.contains(e.target) &&
        e.target !== board_container &&
        !board_container.contains(e.target)
      ) {
        if (toggleKeypad) keypadHide();
      }
      if (
        e.target !== sideBar &&
        !sideBar.contains(e.target) &&
        e.target !== sideBarTrigger &&
        !sideBarTrigger.contains(e.target) &&
        e.target !== mvmenu &&
        !mvmenu.contains(e.target)
      ) {
        if (sideMenuToggle) sideMenuClose();
      }
    };

    document.addEventListener("click", documentClickFn);
    board_container.addEventListener("click", keypadShowFn);
    return () => {
      board_container.removeEventListener("click", keypadShowFn);
      document.removeEventListener("click", documentClickFn);
    };
  }, [keypadHide, toggleKeypad, keypadShow, sideMenuToggle, sideMenuClose]);

  // useEffect(()=>{
  //     const board_container = document.getElementById('board_container');
  //     const keypadShowFn = () => {
