import React, { useEffect } from "react";
import { connect } from "react-redux";
import Header from "../../components/BoardHeaderRow/Row";
import Carry from "../../components/BoardCarryRow/Row";
import Result from "../../components/BoardResultsRow/Row";
import Span from "../../components/BoardCarryRowSpan/Span";
import classNames from "./Board.module.css";
// import * as reduxFunctions from '../../store/actions/index';

const Board = (props) => {
  // const inputChange = (e, i, tb, changeFN)=>{
  //     let maxLength = 1;
  //     if (e.target.value.length > maxLength){
  //         e.target.value = e.target.value.slice(0, maxLength);
  //     }
  //     let old = [...tb]
  //     old[i] = e.target.value;
  //     props.input(old, changeFN);
  // }

  // let nameInput;

  // useEffect(()=>{
  //     nameInput.focus();
  // },[nameInput])

  let rows;

  switch (props.resultRow) {
    case 1:
      rows = (
        <>
          <Result numbers={props.r1} color="r1" blank fRC={props.r4} />
        </>
      );
      break;
    case 2:
      rows = (
        <>
          <Result numbers={props.r1} color="r1" blank fRC={props.r4} />
          <Result numbers={props.r2} color="r2" blankH zeroO />
          <Result numbers={props.r4} color="r4" />
        </>
      );
      break;
    case 3:
      rows = (
        <>
          <Result numbers={props.r1} color="r1" blank fRC={props.r4} />
          <Result numbers={props.r2} color="r2" blankH zeroO />
          <Result numbers={props.r3} color="r3" zero />
          <Result numbers={props.r4} color="r4" />
        </>
      );
      break;

    default:
      rows = null;
      break;
  }
  // const focusColor = (c1, c2) => {
  //     const yellow = {backgroundColor: 'yellow'};
  //     const red = {backgroundColor: 'transparent'};
  //     let color;
  //     if(c1){
  //         color = yellow;
  //     }else if(c2){
  //         color = red;
  //     }else{
  //         color = null;
  //     }
  //     return color;
  // }
  const focusColor = (c1) => {
    const yellow = { backgroundColor: "yellow" };
    let color;
    if (c1) {
      color = yellow;
    } else {
      color = null;
    }
    return color;
  };

  let topRowClasses;
  if (props.grid) {
    topRowClasses = `${classNames.digits} ${classNames.brc}`;
  } else {
    topRowClasses = `${classNames.digits}`;
  }

  let bottomRowClasses;
  if (props.grid) {
    bottomRowClasses = `${classNames.digits} ${classNames.brdr} ${classNames.brd}`;
  } else {
    bottomRowClasses = `${classNames.digits} ${classNames.brdr}`;
  }

  useEffect(() => {
    const boardContainer = document.getElementById("boardContainer");
    boardContainer.scroll({
      top: boardContainer.scrollHeight,
      behavior: "smooth",
    });
  }, [props.resultRow]);

  return (
    <div className={classNames.container} id="boardContainer">
      <div className={classNames.inner}>
        {props.pheader ? <Header /> : null}

        {props.carry ? (
          <>
            <Carry numbers={props.tLofCarry} color="c1" />
            <Carry numbers={props.mLofCarry} color="c2" />
            <Carry numbers={props.bLofCarry} color="c3" />
          </>
        ) : null}

        <div className={classNames.row}>
          <div className={topRowClasses}>
            <h1>{null}</h1>
          </div>
          <div className={topRowClasses}>
            <h1>{null}</h1>
          </div>
          <div className={topRowClasses}>
            <h1>{null}</h1>
          </div>
          <div
            className={topRowClasses}
            // onClick={() => {if(!props.inputDisable){props.inputFocus(0, 'inputFocusTop')}}}
            style={focusColor(props.focusInput.t.first)}
          >
            {!props.carry ? (
              <Span
                n={[
                  props.bLofCarry.hundreds.value,
                  props.mLofCarry.thousands.value,
                  props.tLofCarry.tenThousands.value,
                ]}
              />
            ) : null}
            <h1>{props.tR[0]}</h1>
            {/* <input
                        type='number'
                        onClick={() => props.inputFocus(0, 'inputFocusTop')}
                        value={props.tR[0]}
                        onChange={(e)=>inputChange(e, 0, props.tR, 'topRow')} max="1"
                        style={focusColor(props.focusInput.t.first, props.inputFocusTop[0])}
                        ref={(input) => { nameInput = input; }}
                        disabled={props.inputDisable}
                        /> */}
          </div>
          <div
            className={topRowClasses}
            // onClick={() => {if(!props.inputDisable){props.inputFocus(1, 'inputFocusTop')}}}
            style={focusColor(props.focusInput.t.second)}
          >
            {!props.carry ? (
              <Span
                n={[
                  props.bLofCarry.tens.value,
                  props.mLofCarry.hundreds.value,
                  props.tLofCarry.thousands.value,
                ]}
              />
            ) : null}
            <h1>{props.tR[1]}</h1>
            {/* <input
                        type='number'
                        onClick={() => props.inputFocus(1, 'inputFocusTop')}
                        value={props.tR[1]}
                        onChange={(e)=>inputChange(e, 1, props.tR, 'topRow')} max="1"
                        style={focusColor(props.focusInput.t.second, props.inputFocusTop[1])}
                        disabled={props.inputDisable}
                        /> */}
          </div>
          <div
            className={topRowClasses}
            // onClick={() => {if(!props.inputDisable){props.inputFocus(2, 'inputFocusTop')}}}
            style={focusColor(props.focusInput.t.third)}
          >
            <h1>{props.tR[2]}</h1>
            {/* <input
                        type='number'
                        onClick={() => props.inputFocus(2, 'inputFocusTop')}
                        value={props.tR[2]}
                        onChange={(e)=>inputChange(e, 2, props.tR, 'topRow')}
                        max="1"
                        style={focusColor(props.focusInput.t.third, props.inputFocusTop[2])}
                        disabled={props.inputDisable}
                        /> */}
          </div>
        </div>

        <div className={classNames.row}>
          <div className={`${bottomRowClasses}`}>
            <h1 className={classNames.cross}>X</h1>
          </div>
          <div className={`${bottomRowClasses}`}>
            <h1>{null}</h1>
          </div>
          <div className={`${bottomRowClasses}`}>
            <h1>{null}</h1>
          </div>
          <div
            className={`${bottomRowClasses} ${classNames.bI1}`}
            // onClick={() => {if(!props.inputDisable){props.inputFocus(0, 'inputFocusBottom')}}}
            style={focusColor(props.focusInput.b.first)}
          >
            <h1>{props.bR[0]}</h1>
            {/* <input
                        type='number'
                        onClick={() => props.inputFocus(0, 'inputFocusBottom')}
                        value={props.bR[0]}
                        onChange={(e)=>inputChange(e, 0, props.bR, 'bottomRow')} max="1"
                        style={focusColor(props.focusInput.b.first, props.inputFocusBottom[0])}
                        disabled={props.inputDisable}
                        className={classNames.bI1}
                        /> */}
          </div>
          <div
            className={`${bottomRowClasses} ${classNames.bI2}`}
            // onClick={() => {if(!props.inputDisable){props.inputFocus(1, 'inputFocusBottom')}}}
            style={focusColor(props.focusInput.b.second)}
          >
            <h1>{props.bR[1]}</h1>
            {/* <input
                        type='number'
                        onClick={() => props.inputFocus(1, 'inputFocusBottom')}
                        value={props.bR[1]}
                        onChange={(e)=>inputChange(e, 1, props.bR, 'bottomRow')} max="1"
                        style={focusColor(props.focusInput.b.second, props.inputFocusBottom[1])}
                        disabled={props.inputDisable}
                        className={classNames.bI2}
                        /> */}
          </div>
          <div
            className={`${bottomRowClasses} ${classNames.bI3}`}
            // onClick={() => {if(!props.inputDisable){props.inputFocus(2, 'inputFocusBottom')}}}
            style={focusColor(props.focusInput.b.third)}
          >
            <h1>{props.bR[2]}</h1>
            {/* <input
                        type='number'
                        onClick={() => props.inputFocus(2, 'inputFocusBottom')}
                        value={props.bR[2]}
                        onChange={(e)=>inputChange(e, 2, props.bR, 'bottomRow')} max="1"
                        style={focusColor(props.focusInput.b.third, props.inputFocusBottom[2])}
                        disabled={props.inputDisable}
                        className={classNames.bI3}
                        /> */}
          </div>
        </div>

        {rows}
      </div>
    </div>
  );
};

const mapStatetoProps = (state) => {
  return {
    tR: state.number.topRow,
    bR: state.number.bottomRow,
    // inputDisable: state.number.inputDisable,
    tLofCarry: state.carry.topLine,
    mLofCarry: state.carry.middleLine,
    bLofCarry: state.carry.bottomLine,
    r1: state.result.topResult,
    r2: state.result.middleResult,
    r3: state.result.bottomResult,
    r4: state.result.finalResult,
    focusInput: state.result.focusingInput,
    resultRow: state.number.resutltsRow,
    inputFocusTop: state.numpad.inputFocusTop,
    inputFocusBottom: state.numpad.inputFocusBottom,
    pheader: state.menu.pheader,
    grid: state.menu.grid,
    carry: state.menu.carry,
  };
};
// const mapDispatchtoProps = dispatch => {
//     return{
//         input: (newNum, fn) => dispatch(reduxFunctions.inputNumber(newNum, fn)),
//         inputFocus: (c, fn) => dispatch(reduxFunctions.inputFocus(c, fn))
//     }
// }

export default connect(mapStatetoProps)(Board);
