import * as actionTypes from "../actions/actions";

const initState = {
  topRow: ["", "", ""],
  bottomRow: ["", "", ""],
  boardTitle: {
    title: "",
    show: false,
  },
  counter: 0,
  nextBtnHide: false,
  resutltsRow: 5,
  inputDisable: false,
  btnHide: true,
  trd: 3,
  brd: 3,
};

const reducer = (state = initState, actions) => {
  if (state.counter === 19) {
    return {
      ...state,
      counter: 0,
      nextBtnHide: true,
    };
  }

  if (actions.type === actionTypes.COUNTERRESULT) {
    if (actions.upperDigit === 1 && actions.lowerDigit === 1) {
      if (state.counter === 1) {
        return {
          ...state,
          counter: 18,
        };
      }
    }
    if (actions.upperDigit === 2 && actions.lowerDigit === 1) {
      if (state.counter === 2) {
        return {
          ...state,
          counter: 18,
        };
      }
    }
    if (actions.upperDigit === 2 && actions.lowerDigit === 2) {
      switch (state.counter) {
        case 2:
          return {
            ...state,
            counter: 3,
          };
        case 6:
          return {
            ...state,
            counter: 11,
          };
        case 16:
          return {
            ...state,
            counter: 18,
          };

        default:
          break;
      }
    }
    if (actions.upperDigit === 3 && actions.lowerDigit === 1) {
      if (state.counter === 3) {
        return {
          ...state,
          counter: 18,
        };
      }
    }
    if (actions.upperDigit === 3 && actions.lowerDigit === 2) {
      if (state.counter === 7) {
        return {
          ...state,
          counter: 11,
        };
      } else if (state.counter === 17) {
        return {
          ...state,
          counter: 18,
        };
      }
    }
  }
  if (actions.type === actionTypes.COUNTERPREV) {
    if (actions.upperDigit === 1 && actions.lowerDigit === 1) {
      if (state.counter === 18) {
        return {
          ...state,
          counter: 0,
        };
      }
    }
    if (actions.upperDigit === 2 && actions.lowerDigit === 1) {
      if (state.counter === 18) {
        return {
          ...state,
          counter: 1,
        };
      }
    }
    if (actions.upperDigit === 2 && actions.lowerDigit === 2) {
      switch (state.counter) {
        case 3:
          return {
            ...state,
            counter: 1,
          };
        case 11:
          return {
            ...state,
            counter: 5,
          };
        case 18:
          return {
            ...state,
            counter: 15,
          };

        default:
          break;
      }
    }
    if (actions.upperDigit === 3 && actions.lowerDigit === 1) {
      if (state.counter === 18) {
        return {
          ...state,
          counter: 2,
        };
      }
    }
    if (actions.upperDigit === 3 && actions.lowerDigit === 2) {
      if (state.counter === 11) {
        return {
          ...state,
          counter: 6,
        };
      } else if (state.counter === 18) {
        return {
          ...state,
          counter: 16,
        };
      }
    }
  }

  const getLength = (r) => {
    const d = [];
    r.forEach((c) => {
      if (Number(c)) d.push(c);
    });
    return d.length;
  };

  const getPosition = (p) => {
    if (p === "topRow") {
      return state.topRow;
    } else if (p === "bottomRow") {
      return state.bottomRow;
    }
  };

  const randomNumberGenerate = (digit) => {
    let randomNumArray;
    const randomNum1 = Math.floor(Math.random() * 9) + 1;
    const randomNum2 = Math.floor(Math.random() * 9) + 1;
    const randomNum3 = Math.floor(Math.random() * 9) + 1;
    const randomNumString1 = randomNum1.toString();
    const randomNumString2 = randomNum2.toString();
    const randomNumString3 = randomNum3.toString();

    switch (digit) {
      case 3:
        randomNumArray = [randomNumString1, randomNumString2, randomNumString3];
        break;
      case 2:
        randomNumArray = ["", randomNumString1, randomNumString2];
        break;
      case 1:
        randomNumArray = ["", "", randomNumString1];
        break;

      default:
        randomNumArray = [randomNumString1, randomNumString2, randomNumString3];
        break;
    }

    return randomNumArray;
  };

  const startingObject = (top, bottom) => {
    const t = top.join("");
    const b = bottom.join("");

    return {
      boardTitle: {
        title: `We will multiply <span>${top[0]}${top[1]}${
          top[2]
        }</span> x <span>${bottom[0]}${bottom[1]}${
          bottom[2]
        }</span>. The answer is <span>${
          t * b
        }</span>, but that doesn’t matter. We don’t care about the answer here; we care only about how to get it. <span>Click Next</span>`,
        show: true,
      },
      nextBtnHide: false,
      resutltsRow: b.length,
      inputDisable: true,
      btnHide: false,
    };
  };

  const endingObject = {
    ...state,
    topRow: ["", "", ""],
    bottomRow: ["", "", ""],
    boardTitle: {
      ...state.boardTitle,
      show: false,
    },
    counter: 0,
    resutltsRow: 5,
    inputDisable: false,
    btnHide: true,
    trd: getLength(state.topRow),
    brd: getLength(state.bottomRow),
  };

  switch (actions.type) {
    case actionTypes.KEYPRESS:
      // let newArrayOnPress, keys, p;
      let p = getPosition(actions.position);
      p.shift();
      p.push(actions.key);
      // keys = p.keys();
      // newArrayOnPress = p.map((el)=>{
      //     for(const key of keys){
      //         if(key === actions.index){
      //             return actions.key
      //         }else{
      //             return el
      //         }
      //     }
      //     return el
      // })
      return {
        ...state,
        // [actions.position]: newArrayOnPress,
      };
    case actionTypes.DELETEDIGIT:
      // let newArrayOnDelete, keysD, positionOfDigit;
      // positionOfDigit = getPosition(actions.position);
      // keysD = positionOfDigit.keys();
      // newArrayOnDelete = positionOfDigit.map((el)=>{
      //     for(const key of keysD){
      //         if(key === actions.index){
      //             return ''
      //         }else{
      //             return el
      //         }
      //     }
      //     return el
      // })
      let pDel = getPosition(actions.position);
      pDel.pop();
      pDel.unshift("");

      return {
        ...state,
        // [actions.position]: newArrayOnPress,
      };
    // case actionTypes.RANDOM_NUMBER:
    //     let randomNumArray = randomNumberGenerate(actions.digitCount);
    //     return{
    //         ...state,
    //         [actions.rowType]: randomNumArray
    //     }
    // case actionTypes.INPUT_NUMBER:
    //     return{
    //         ...state,
    //         [actions.rowPosition]: actions.newNumbers
    //     }
    case actionTypes.START:
      const start = startingObject(state.topRow, state.bottomRow);
      return {
        ...state,
        ...start,
      };
    case actionTypes.GENERATEONSTART:
      let topRowNew, bottomRowNew;
      if (getLength(state.topRow) > 0 && getLength(state.bottomRow) !== 0) {
        topRowNew = randomNumberGenerate(getLength(state.topRow));
      } else {
        topRowNew = randomNumberGenerate(state.trd);
      }
      if (getLength(state.bottomRow) > 0) {
        bottomRowNew = randomNumberGenerate(getLength(state.bottomRow));
      } else {
        bottomRowNew = randomNumberGenerate(state.brd);
      }
      if (getLength(state.topRow) < getLength(state.bottomRow)) {
        topRowNew = randomNumberGenerate(3);
        bottomRowNew = randomNumberGenerate(3);
      }
      if (state.trd < state.brd) {
        topRowNew = randomNumberGenerate(3);
        bottomRowNew = randomNumberGenerate(3);
      }
      const startOnGen = startingObject(topRowNew, bottomRowNew);
      return {
        ...state,
        topRow: topRowNew,
        bottomRow: bottomRowNew,
        ...startOnGen,
      };
    case actionTypes.END:
      return {
        ...endingObject,
      };
    case actionTypes.COUNTER:
      return {
        ...state,
        counter: state.counter + 1,
      };
    case actionTypes.COUNTERPREV:
      if (state.counter === 0) {
        return {
          ...endingObject,
        };
      } else {
        return {
          ...state,
          counter: state.counter - 1,
        };
      }
    default:
      return state;
  }
};

export default reducer;
