import React from "react";
import classNames from "./Span.module.css";

const Span = (props) => {
  return (
    <>
      {props.n.map((c, i) => {
        if (c) {
          return (
            <span key={i} className={classNames.carry}>
              {c}
            </span>
          );
        } else return null;
      })}
    </>
  );
};

export default Span;
